* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
    padding: 0;
    margin: 0;
}
/*============================================================= Our Team start ========================================================*/

.ourteam {
    /* width: 100%;
    height:100% ; */
    height: auto;
    width: auto;
    background: #fff;
}

.ourteam img {
    width: 100%;
    height: 100%;
    border-bottom-left-radius: 4%;
    border-bottom-right-radius: 4%;
}



/*============================================================= Our Team end ========================================================*/

/*============================================================= mid start ========================================================*/

.mid{
    height: 100%;
    width: 100%;
}
.mannage-contact{
    height: 100%;
    width: 100%;
    /* margin-right: 10%; */
}
.map{
    width: 66%;
    /* margin-top: 5%; */
    /* margin-left: 10%; */
    display: flex;
    margin: auto;
}
.map iframe{
    margin-top: 30px;
}
.mannage-contact h1{
    text-align: center;
    margin-bottom: 5%;
    margin-top: 5%;
    font-size: 30px;
}
.details{
   margin-left: 30px;
   height: 500px;

}
.details div{
    margin-bottom: 30px;
    height: 160px;
    width: 350px;
    border-radius: 15px;
    box-shadow: 2px 2px 2px 2px rgb(244, 240, 240);
    padding-top: 10px;
}
.details div i{
    margin-left: 25px;
    /* margin-top: 4px; */
    font-size: 40px;
}
.details div h3{
    margin-left: 25px;
    margin-top: 8px;
}
.details div p{
    margin-left: 25px;
    margin-top: 8px;
}
/*============================================================= mid end ========================================================*/

 /* ======================================================= last section start ======================================================  */
 .last{
    margin-top: 100px;
    height: 100%;
    width: 100%;
    background-color:  rgb(236, 249, 243);
    padding-bottom: 100px;
 }
 .last h1{
    text-align: center;
    font-size: 40px;
    padding-top: 70px;
    padding-bottom: 10px;
 }
 .last p{
    width: 52%;
    text-align: center;
    margin: auto;
}
.last .contact{
    width: 53%;
    /* padding-left: 20%; */
    /* padding-right: 20%; */
    padding-top: 2%;
    margin: auto;
}
.contact input{
    height: 50px;
    width: 350px;
    box-shadow: 5px 8px 39px 8px rgb(229, 245, 238);
    border: none;
    border-radius: 10px;
    padding-left: 20px;
    font-size: 15px;

}
.contact input:focus{
    outline: none;
}
.contact input[type=text1]{
    padding-left: 20px;
}
/* .input-data{
    box-shadow: 3px 3px 3px 3px grey;
} */
.last div{
    display: grid;
    grid-template-columns: auto auto auto;
    /* gap: 20px 50px; */
}
.contact textarea{
    font-size: 15px;
    padding-left: 20px;
    padding-top: 20px;
    max-height: 100px;
    min-height: 100px;
    max-width: 733px;
    min-width: 733px;
    border: none;
    box-shadow: 2px 2px 2px 2px rgb(236, 249, 243);
}
.contact textarea:focus{
    outline: none;
}
.button1{
   background-color:darkblue;
   color: white;
   height: 40px;
   width: 102px;
   margin-left: 45%;
   margin-top: 20px;
   border-radius: 8px;
   display: block;
   border: none;
   font-size: 16px;
   cursor: pointer;
   text-align: center;
}
.button1:hover{
    background-color: #003cff;
    color: white;
}
.input-data label{
    position:absolute;
    padding-top: 20px;
    padding-left: 10px;
    transition: all 0.3s ease;
    color: grey;
    z-index:1;
    font-size: 14px;
}
.input-data input:focus ~ label,
.input-data input:valid ~label{
    transform:translateX(-1px);
    margin-top:-37px;
}
 /* ======================================================= last section end ======================================================  */